/* Textbox styling for solving red asterisk in the placeholder */
html {
  --input-text: black;
  --input-bg: white;
  --label-bg: white;
  --label-bg-disabled: linear-gradient(180deg, white 50%, #F8F8F8 50%);
  --input-border: black;
}

html[data-theme=darkmode] {
  --input-text: white;
  --input-bg: #383838;
  --label-bg: linear-gradient(180deg, #181A1B 50%, #383838 50%);
  --label-bg-disabled: linear-gradient(180deg, #181A1B 50%, #AFAFAF 50%);
  --input-border: #737373;
}

.textboxEditor {
  position: relative;
  margin-bottom: 1em;
}
.textboxEditor__label_border {
  font-size: 0.7em;
  position: absolute;
  top: -0.6em;
  left: 1em;
  padding: 0 0.25em;
  background: var(--label-bg);
}
.textboxEditor__label_border.textboxEditor__label_disabled {
  background: var(--label-bg-disabled);
}
.textboxEditor__input {
  display: block;
}
.textboxEditor__input:not([disabled]) {
  background-color: var(--input-bg);
  color: var(--input-text);
  border: 1px solid var(--input-border);
  border-radius: 2px;
}
.textboxEditor__placeholder {
  position: absolute;
  top: 0;
  left: 0.3rem;
  height: 2.375rem;
  display: none;
}
.textboxEditor__placeholderLabel {
  color: grey;
  user-select: none;
  cursor: text;
}
.textboxEditor__asterisk {
  color: red;
}
.textboxEditor_iconOnLeft {
  display: flex;
}
.textboxEditor_iconOnLeft > .textboxEditor__placeholder {
  left: 2.7rem;
}
.textboxEditor__iconContainer {
  width: 2.5em;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid lightgrey;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}
.textboxEditor__iconContainer + .textboxEditor__input {
  border-top-left-radius: unset;
  border-bottom-left-radius: unset;
  border-left-width: 0;
}
.textboxEditor__iconContainer > .textboxEditor__icon {
  min-width: 1.125em;
  min-height: 1.125em;
}

:not(.textboxEditor__label_none).textboxEditor__label_required::after {
  content: "*";
  color: red;
}